export const addQuiztest = async (data,tags,typeQuiz,ListQuiz) => {
  try {
    let newData = {
          name : data.name,
          tags : tags,
          typeQuiz : typeQuiz,
          listQuiz : ListQuiz
    }
    let QuizTestRef = await app.firestore().collection('quiz_test').add(newData);
    let quiz_test = await QuizTestRef.get();
    sendNotify(tags, newData);
    return quiz_test.data();
  } catch (err) {
    console.log(err);
  }
};

